import * as React from 'react';
import { StyledEngineProvider } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Header from './components/header'
import VOCABULARY from './vocabulary'

export default function App() {
  function handleChange() {
    var text = document.getElementById("input").value
      .replace(/(<([^>]+)>)/gi, " ")
      .replace(/\r?\n| /g, " ") //remove \n and special character " "
      .replace(/\s-/g, " ") // like "-You", "-It's", used for someone's comments
      .replace(/Â/g, "")
      .replace(/\s\s/g, "");
    var words = text.replace(/\.|,|\?|!|:|;|\$|"/g, "")
      .replace(/\[.*?\]\s/g, "") // remove [ABC], take care to remove \s after "]"
      .replace(/--/g, " ") // "--" is used like ...
      .replace(/\s\s/g, "")
      .toLowerCase()
      .split(" ");
    words = Array.from(new Set(words)); // remove duplicate words

    document.getElementById("subtitle").value = text;
    document.getElementById("vocabulary").value = trimGSLWords(words).join("\n");
  }
  function trimGSLWords(words) {
    return words.filter(
      item =>
        // remove contraction (I'm) and words contain number (1940s)
        // match() returns null or value, so convert to t/f by "!!"
        !(!!item.match(/'|\d/g)) &&
        item.length >= 3 &&
        !VOCABULARY.includes(item) 
    );
  }

  return (
    <StyledEngineProvider injectFirst>
      <Grid container spacing={2} >
        <Header />
        <Grid item xs={12} container justifyContent="center" >
          <TextField
            id="input"
            label="Input subtitle xml data"
            multiline
            rows={30}
            defaultValue='<?xml version="1.0" encoding="UTF-8" standalone="no"?>
            <p begin="00000000t" end="11111111t" regi on="bottom" xml:id="subtitle0"><span style="span">[Narrator] </span><span style="italic">Once upon a time, Little Red Riding Hood was on her way to visit her sick grandmother,....</span></p>'
            style={{
              margin: 'auto',
              width: '40%'
            }}
            onChange={(e) => handleChange(e)}
          />
          <TextField
            id="subtitle"
            label="Subtitle"
            multiline
            rows={30}
            defaultValue="[Narrator] Once upon a time, Little Red Riding Hood was on her way to visit her sick grandmother,.... "
            style={{
              margin: 'auto',
              width: '40%',
              backgroundColor: "#F2F2F2"
            }}
          />
          <TextField
            id="vocabulary"
            label="Vocabulary"
            multiline
            rows={30}
            defaultValue="hood&#10;grandmother"
            style={{
              margin: 'auto',
              width: '15%',
              backgroundColor: "#F2F2F2"
            }}
          />
        </Grid>
      </Grid>
    </StyledEngineProvider>
  );
}

